  font-family: lemon-milk;
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  color: black;
  padding: 10%;
}

.dropdown {
  color: white;
  display: inline-block;
  width: 25%;
}

.dropdown__content {
  background-color: rgb(255,255,255, .7);
  display: none;
  position: absolute;
  text-decoration: none;

}

.dropdown--active .dropdown__content {
  display: block;
  margin-top: 50px;
  z-index: 1;
}

.icon {
  margin: 10px;
}

